import create from 'zustand';
import { useEffect, useMemo } from 'react';
import shallow from 'zustand/shallow';
import { Doc } from 'yjs';

var YDocEnum;

(function (YDocEnum) {
  YDocEnum[YDocEnum["GUID"] = 0] = "GUID";
  YDocEnum[YDocEnum["DOC"] = 1] = "DOC";
  YDocEnum[YDocEnum["MOUNT"] = 2] = "MOUNT";
  YDocEnum[YDocEnum["UN_MOUNT"] = 3] = "UN_MOUNT";
  YDocEnum[YDocEnum["USE_CNT"] = 4] = "USE_CNT";
})(YDocEnum || (YDocEnum = {}));

var ObserveEnum;

(function (ObserveEnum) {
  ObserveEnum[ObserveEnum["TYPE"] = 0] = "TYPE";
  ObserveEnum[ObserveEnum["OBSERVE"] = 1] = "OBSERVE";
  ObserveEnum[ObserveEnum["USE_CNT"] = 2] = "USE_CNT";
})(ObserveEnum || (ObserveEnum = {}));

const useYStore = create((set, get) => {
  return {
    yDocs: [],
    observers: [],
    data: [],

    update(type, data) {
      const dataSet = get().data;
      set({
        data: [...dataSet.filter(([t]) => t !== type), [type, data]]
      });
    },

    listenType(type, listen) {
      const observers = get().observers;
      const match = observers.find(([observer]) => observer === type);

      if (match) {
        set({
          observers: [...observers.filter(obs => obs !== match), [match[ObserveEnum.TYPE], match[ObserveEnum.OBSERVE], match[ObserveEnum.USE_CNT] + 1]]
        });
      } else {
        type.observe(listen);
        set({
          observers: [...observers, [type, listen, 1]]
        });
      }
    },

    unListenType(type) {
      const observers = get().observers;
      const match = observers.find(([t]) => t === type);

      if (!match) {
        console.warn('Type ', type, " wasn't mounted");
        return;
      }

      const observerCount = match[ObserveEnum.USE_CNT] - 1;

      if (observerCount > 0) {
        set({
          observers: [...observers.filter(obs => obs !== match), [match[ObserveEnum.TYPE], match[ObserveEnum.OBSERVE], observerCount]]
        });
      } else {
        type.unobserve(match[ObserveEnum.OBSERVE]);
        set({
          observers: [...observers.filter(obs => obs !== match)],
          data: [...get().data.filter(([t]) => t !== type)]
        });
      }
    },

    unMountYDoc(yDoc) {
      const yDocs = get().yDocs;
      const match = yDocs.find(([, doc]) => doc === yDoc);

      if (!match) {
        console.warn('Can not unmount doc ', yDoc.guid, ' . Doc not found');
        return;
      }

      const observerCount = match[YDocEnum.USE_CNT] - 1;

      if (observerCount > 0) {
        set({
          yDocs: [...yDocs.filter(yDoc => yDoc !== match), [match[YDocEnum.GUID], match[YDocEnum.DOC], match[YDocEnum.MOUNT], match[YDocEnum.UN_MOUNT], observerCount]]
        });
      } else {
        const unMount = match[YDocEnum.UN_MOUNT];
        if (unMount) unMount();
        set({
          yDocs: yDocs.filter(yDoc => yDoc !== match)
        });
      }
    },

    mountYDoc(yDoc, mount) {
      const yDocs = get().yDocs;
      const match = yDocs.find(([name]) => name === yDoc.guid);

      if (match) {
        if (match[YDocEnum.MOUNT] !== mount) {
          console.warn('A different connection function was already provided for ', name, '. This unMount function will be ignored');
        }

        match[YDocEnum.USE_CNT]++;
        set({
          yDocs: [...yDocs.filter(([name]) => name !== yDoc.guid), match]
        });
      } else {
        const unMount = mount(yDoc);
        set({
          yDocs: [...yDocs, [yDoc.guid, yDoc, mount, unMount, 1]]
        });
      }
    }

  };
});

const useYObserve = (yType, serialize) => {
  const [listen, unListen, update] = useYStore(state => [state.listenType, state.unListenType, state.update], shallow);
  useEffect(() => {
    listen(yType, () => update(yType, serialize()));
    update(yType, serialize());
    return () => unListen(yType);
  }, []);
};

const useYArray = yArray => {
  useYObserve(yArray, () => yArray.toArray());
  const dataSet = useYStore(state => state.data);
  const data = useMemo(() => {
    const match = dataSet.find(([type]) => type === yArray);
    if (!match) return [];
    return match[1];
  }, [yArray, dataSet]);

  const noBinding = funcKey => () => {
    throw new Error(`Y.Array#${funcKey} is not implemented`);
  };

  return {
    forEach: yArray.forEach ? yArray.forEach.bind(yArray) : noBinding('forEach'),
    map: yArray.map ? yArray.map.bind(yArray) : noBinding('map'),
    slice: yArray.slice ? yArray.slice.bind(yArray) : noBinding('slice'),
    get: yArray.get ? yArray.get.bind(yArray) : noBinding('get'),
    delete: yArray.delete ? yArray.delete.bind(yArray) : noBinding('delete'),
    unshift: yArray.unshift ? yArray.unshift.bind(yArray) : noBinding('unshift'),
    push: yArray.push ? yArray.push.bind(yArray) : noBinding('push'),
    insert: yArray.insert ? yArray.insert.bind(yArray) : noBinding('insert'),
    data
  };
};

const useYMap = yMap => {
  useYObserve(yMap, () => yMap.toJSON());
  const dataSet = useYStore(state => state.data);
  const data = useMemo(() => {
    const match = dataSet.find(([type]) => type === yMap);
    if (!match) return {};
    return match[1];
  }, [yMap, dataSet]);

  const noBinding = funcKey => () => {
    throw new Error(`Y.Map#${funcKey} is not implemented`);
  };

  return {
    keys: yMap.keys ? yMap.keys.bind(yMap) : noBinding('keys'),
    values: yMap.values ? yMap.values.bind(yMap) : noBinding('values'),
    entries: yMap.entries ? yMap.entries.bind(yMap) : noBinding('entries'),
    forEach: yMap.forEach ? yMap.forEach.bind(yMap) : noBinding('forEach'),
    delete: yMap.delete ? yMap.delete.bind(yMap) : noBinding('delete'),
    set: yMap.set ? yMap.set.bind(yMap) : noBinding('set'),
    get: yMap.get ? yMap.get.bind(yMap) : noBinding('get'),
    has: yMap.has ? yMap.has.bind(yMap) : noBinding('has'),
    data
  };
};

const useYDoc = (name, mount) => {
  const [mountYDoc, unMountYDoc, yDocs] = useYStore(state => [state.mountYDoc, state.unMountYDoc, state.yDocs], shallow);
  const yDoc = useMemo(() => {
    const match = yDocs.find(([docGuid]) => docGuid === name);
    if (match) return match[YDocEnum.DOC];
    const yDoc = new Doc();
    yDoc.guid = name;
    mountYDoc(yDoc, mount);
    return yDoc;
  }, [yDocs]);
  useEffect(() => {
    return () => unMountYDoc(yDoc);
  }, [unMountYDoc]);
  return yDoc;
};

export { useYArray, useYDoc, useYMap };
